import React, { useContext, useRef } from 'react';
import PropTypes from 'prop-types';
import * as styles from './HomeVideo.module.scss';
import { GeneralContext } from '../../context/GeneralContext';

export default function HomeVideo({ vidURL }) {
  const modalValue = useContext(GeneralContext);
  const closeBtn = useRef();
  const modalBG = useRef();

  const closeModal = (e) => {
    if (
      e.target === closeBtn.current ||
      e.target === modalBG.current ||
      e.target.parentNode === closeBtn.current
    ) {
      modalValue.setHomeVideoModal(false);
    }
  };

  return (
    <div
      className={`${modalValue.homeVideoModal ? styles.openModal : ''} ${styles.wrapper}`}
      onClick={closeModal}
      ref={modalBG}
    >
      <div className={`${styles.holder}`}>
        <button
          className={`${styles.closeModal} mb-auto`}
          type='button'
          onClick={closeModal}
          ref={closeBtn}
          aria-label='Closes popup video about company at start of homepage'
        >
          <h3>X</h3>
        </button>

        <div className='w-100 h-auto mx-auto text-center'>
          {!vidURL && (
            <h4>
              Video not found, please refresh page and click the play button.
              <small> Generic Video goes here</small>
            </h4>
          )}
          {vidURL && (
            <>
              <h2> {vidURL} </h2>
              <h3>Video Popup </h3>
              <small>Video URL passed in as prop loads here</small>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

HomeVideo.defaultProps = {
  vidURL: 'Generic Video When None is Provided',
};

HomeVideo.propTypes = {
  vidURL: PropTypes.string,
};
