import React from 'react';

import Layout from '@layouts';
import { Banners, SEO, EducationFund } from '@components';
import HomeVideo from '../popups/HomeVideo';

const { EducationFoundBanner } = Banners;

const Index = () => (
  <Layout
    bannerStyle={{ backgroundImage: 'none' }}
    bannerChildren={
      <EducationFoundBanner title='Meet Nigerian Temiloluwa O. Prioleau, the first Black woman tenure-track faculty in Computer Science in the Ivy League' />
    }
    heroBackgroundColor='altlightest'
  >
    <SEO title='TAIK EDUCATION FUND' />
    <EducationFund />
    <HomeVideo vidURL='Education Fund Page Vid Url Passed as Prop' />
  </Layout>
);

export default Index;
